import { Nullable } from "../../Common/Types";

export type Role =
'User' |
'Admin' |
'Moderator' |
'Banned'

export const localizeRole = (role: Role): Nullable<string> => {
  let localizedRole: string | undefined = undefined;
  switch (role) {
    case 'Admin': {
      localizedRole = '';
      break;
    };
    case 'User': {
      localizedRole = '';
      break;
    };
    case 'Moderator': {
      localizedRole = '';
      break;
    }
    case 'Banned': {
      localizedRole = '';
      break;
    };
  };
  return localizedRole;
}